import axios from 'axios';

const SHIPROCKET_API_URL = 'https://apiv2.shiprocket.in/v1/external'; // Replace with actual API URL

export default async function handler(req, res) {
  const { method, query, body } = req;

  switch (method) {
    case 'GET':
      // Fetch orders
      try {
        const response = await axios.get(`${SHIPROCKET_API_URL}/orders`);
        res.status(200).json(response.data);
      } catch (error) {
        res.status(500).json({ error: 'Error fetching orders' });
      }
      break;

    case 'PUT':
      // Update shipment details
      const { shipmentId } = query;
      try {
        const response = await axios.put(`${SHIPROCKET_API_URL}/orders/${shipmentId}`, body);
        res.status(200).json(response.data);
      } catch (error) {
        res.status(500).json({ error: `Error updating shipment ${shipmentId}` });
      }
      break;

    case 'DELETE':
      // Cancel shipment
      const { shipmentId: cancelShipmentId } = query;
      try {
        const response = await axios.delete(`${SHIPROCKET_API_URL}/orders/${cancelShipmentId}`);
        res.status(200).json(response.data);
      } catch (error) {
        res.status(500).json({ error: `Error canceling shipment ${cancelShipmentId}` });
      }
      break;

    case 'POST':
      // Generate Label/Invoice
      const { shipmentId: generateShipmentId, type } = query;
      try {
        const response = await axios.post(`${SHIPROCKET_API_URL}/orders/${generateShipmentId}/generate/${type}`);
        res.status(200).json(response.data);
      } catch (error) {
        res.status(500).json({ error: `Error generating ${type}` });
      }
      break;

    default:
      res.status(405).json({ error: 'Method Not Allowed' });
      break;
  }
}
